import { Injectable } from '@angular/core'
import { Resolve, Router } from '@angular/router';
import { UserService } from "../_services/user.service";
import { AlertifyService } from "../_services/alertify.service";
import { ActivatedRouteSnapshot, RouterStateSnapshot } from "@angular/router/router";
import { Observable, of } from "rxjs";
import { catchError } from "rxjs/operators";
import { Message } from "../_models/message";
import { AuthService } from "../_services/auth.service";

@Injectable()
export class MessagesResolver implements Resolve<Message[]> {
  pageNumber = 1;
  pageSize = 5;
  messageContainer ='Unread';
  constructor(private userService: UserService, private router: Router, private alertify: AlertifyService,
    private authService:AuthService) { }

  resolve(route: ActivatedRouteSnapshot): Observable<Message[]> | Observable<any> {
    return this.userService.getMessages(this.authService.decodedToken.nameid, this.pageNumber, this.pageSize , this.messageContainer).pipe(
      catchError(error => {
        this.alertify.error('Problem retrieving message');
        this.router.navigate(['/home']);
        return of(null);
      }));
  }
}
